    import cj from "@/common/crypto-js.js"
	export default {
		data() {
			return {
				oldPwd: '',
                oldFalg: false,
                newPwd: '',
                newFlag: false,
                comfirePwd: '',
                comfireFalg: false
			}
		},
		methods: {
            checkOld(){
                if (this.oldPwd != '') {
                    this.oldFalg = true;
                    return true
                } else {
                    this.oldFalg = false;
                    uni.showToast({
                        title: '原密码不能为空',
                        icon: "none",
                        mask: true
                    });
                    setTimeout(function(){
                        uni.hideToast();
                    },2000)
                    return false
                }
            },
			checkNew(){
                let pwd_regExp = /^(?![a-zA-Z]+$)(?![0-9]+$)[A-Za-z0-9]{6,15}$/;
                if (this.newPwd != '' && pwd_regExp.test(this.newPwd)) {
                    this.newFlag = true;
                    return true
                } else {
                    this.newFlag = false;
                    uni.showToast({
                        title: '请输入6-15位的字母数字组合',
                        icon: "none",
                        mask: true
                    });
                    setTimeout(function(){
                        uni.hideToast();
                    },2000)
                    return false
                }
            },
            checkComfire(){
                if (this.comfirePwd != '' && this.newPwd === this.comfirePwd) {
                    this.comfireFalg = true;
                    return true
                } else {
                    this.comfireFalg = false;
                    uni.showToast({
                        title: '两次密码必须相同',
                        icon: "none",
                        mask: true
                    });
                    setTimeout(function(){
                        uni.hideToast();
                    },2000)
                    return false
                }
            },
            submit(){
                if (this.checkOld() && this.checkNew() && this.checkComfire()) {
                    let oldPwdStr = cj.MD5(this.oldPwd).toString();
                    let newPwdCofStr = cj.MD5(this.newPwd).toString();
                    let okPwdCofStr = cj.MD5(this.comfirePwd).toString();
                    let tokenStr = this.$store.state.userInfo.loginToken;
                    uni.request({
                        url: this.websiteUrl + '/api/user/upd',
                        method: 'POST',
                        header: {
                            'Authorization': tokenStr
                        },
                        data: {
                            oldUserPwd: oldPwdStr,
                            userPwd: newPwdCofStr,
                            confirmUserPwd: okPwdCofStr 
                        },
                        success: res => {
                            console.log(res)
                            if (res.data && res.data.status === 0) {
                                uni.showToast({
                                    title: '修改成功',
                                    mask: true
                                });
                                setTimeout(function () {
                                    uni.hideToast();
                                    uni.redirectTo({
                                         url: "/pages/login/login",
                                         animationType: "slide-in-left"
                                     });
                                },1600)
                            } else if (res.data.status == 1001) {
                                uni.showToast({
                                    title: res.data.message,
                                    icon: 'none',
                                    mask: true
                                });
                                setTimeout(function () {
                                    uni.hideToast();
                                    uni.navigateTo({
                                        url:"/pages/login/login",
                                        animationType:"slide-in-right"
                                    });
                                }, 300)
                            } else {
                                uni.showToast({
                                    title: res.data.message,
                                    icon: "none",
                                    mask: true
                                });
                                setTimeout(function(){
                                    uni.hideToast();
                                },2000)
                            }
                        },
                        fail: () => {},
                        complete: () => {}
                    });
                } else{
                    return false;
                }
            }
		}
	}
